<template>
  <div class="wrapper">
    <side-bar>
      
      <template slot="links">
        <sidebar-link  to="/quick-find"><img class="custom-icon"  src="@/assets/havr-icon/quickFind.svg"> <p class="custom-label">Quick find</p></sidebar-link>
         <sidebar-link  to="/overview"><img class="custom-icon"  src="@/assets/havr-icon/overview.svg"> <p class="custom-label">Overview</p></sidebar-link>
          <sidebar-link  to="/keychain"><img class="custom-icon"  src="@/assets/havr-icon/keychain.svg"> <p class="custom-label">Keychain</p></sidebar-link>
         <sidebar-link  to="/users"><img class="custom-icon"  src="@/assets/havr-icon/users.svg"> <p class="custom-label">Users</p></sidebar-link>
      </template>
      <mobile-menu>
       
        <drop-down class="nav-item"
                   title="5 Notifications"
                   title-classes="nav-link"
                   icon="ti-bell">
       
        </drop-down>
        <li class="nav-item">
          <a class="nav-link">
            <i class="ti-settings"></i>
            <p>Keychain</p>
          </a>
        </li>
        <li class="divider"></li>
      </mobile-menu>
    </side-bar>
    <div class="main-panel">
      <top-navbar></top-navbar>

      <dashboard-content @click.native="toggleSidebar">

      </dashboard-content>

     
      <content-footer v-if="getCurrentRoute === 'Keychain'"></content-footer>
       <user-footer v-if="getCurrentRoute === 'Users'"></user-footer>
    </div> 
  </div>
</template>
<style lang="scss">
</style>
<script>
import TopNavbar from "./TopNavbar.vue";
import ContentFooter from "./ContentFooter.vue";
import UserFooter from './UserTableFooter'
import DashboardContent from "./Content.vue";
import MobileMenu from "./MobileMenu";
import { dataRepository } from "@/repository/login";
export default {
  components: {
    TopNavbar,
    ContentFooter,
    DashboardContent,
    MobileMenu,
    UserFooter
  },
  mounted() {
    if (!dataRepository.account.access_token)
      return this.$router.push("/login");
    else this.$router.push("/keychain");
  },
  computed: {
    getCurrentRoute() {
      return this.$route.name;
    }
  },
  methods: {
    toggleSidebar() {
      if (this.$sidebar.showSidebar) {
        this.$sidebar.displaySidebar(false);
      }
    }
  }
};
</script>

<style>
.custom-icon {
  font-size: 22px;
  float: left;
  margin-right: 15px;
  line-height: 30px;
  width: 22px;
  position: relative;
  top: 2px;
  text-align: center;
}
.custom-label {
 margin: 0;
    line-height: 30px;
    font-size: 17px;
  font-weight: bold;
  color: #a6a6a6;
}
</style>
