<template>
<div>
  <table class="table margin" :class="tableClass">
    <thead>
    <slot name="columns">
      <th class="capitalize" v-for="column in columns" :key="column">{{column}} <img class="filter-th" src="@/assets/havr-icon/filterTableIcon.svg"></th>
    </slot>
    </thead>
    <tbody>
    <tr v-for="(item, index) in data" :key="index">
      <slot :row="item">
        <td  style="cursor: pointer;" @click='setUserDetails(item)' class="margin-td capitalize" v-for="(column, index) in columns"
            :key="index"
      >
          <div  class="container-table"><div class="valid" v-if="column === 'status' && item[column] === 'active'"><i class="ti-check light"></i></div>
          <div v-if="column === 'name'"><img v-if="item.profile_picture" class="table-profile-picture" :src="'data:image/png;base64,' + item.profile_picture"> <img v-else class="table-profile-picture" src="@/assets/img/usermock.jpeg"></div>
          <div  v-if="column === 'status' && item[column] === 'inactive'"><i class="ti-close inactive"></i></div>
           {{item[column]}}</div>
        </td>
      </slot>
    </tr>
    </tbody>
  </table>
</div>
</template>
<script>
import store from "@/flulux/store";
import lodash from "lodash";
import notifiy from "vue-notification";
import userRepository from "@/repository/users";
import { userDataRepository } from "@/repository/users";
export default {
  data() {
    return {
      store: store,
      notificationToReturn: [],
      accessManagement: {
        admin: [],
        users: []
      }
    };
  },
  props: {
    columns: Array,
    data: Array,
    type: {
      type: String, // striped | hover
      default: "striped"
    },
    maxRow: {
      type: Number,
      default: 0
    },
    title: {
      type: String,
      default: ""
    },
    subTitle: {
      type: String,
      default: ""
    }
  },
  computed: {
    tableClass() {
      return `table-${this.type}`;
    }
  },
  mounted() {},
  methods: {
    hasValue(item, column) {
      return item[column.toLowerCase()] !== "undefined";
    },
    itemValue(item, column) {
      return item[column.toLowerCase()];
    },
    async setUserDetails(p) {
      const obj = lodash.filter(userDataRepository.contacts, {
        user: {id : p.id} 
      });
      obj[0].shared_keys = await userRepository.retrieveSharedKeyForUser(p.id);
      obj[0].logs = await userRepository.retrieveLockDoorUserDetail(p.id);
      const setDetailsUser = await this.store.actions.SET_DETAILS_USER(obj[0]);
      const drawer = await this.store.actions.OPEN_USER_DETAIL_DRAWER(true);
      
    
    }
  }
};
</script>

<style>
.table-profile-picture {
position: relative;
    width: 32px;
    border-radius: 50%;
    height: 32px;
    bottom: 5px;
    margin-right: 5px;
}
</style>
