import { dataRepository } from '@/repository/login'
import { ENDPOINTS, API_BASE_URL } from '../constant.js'

export default {
    async addContact(p) {
        const res = await fetch(API_BASE_URL + ENDPOINTS.add_contact, {
            method: 'post',
            headers: { "Content-Type": "application/json", 'Authorization': 'Bearer: ' +  dataRepository.account.access_token },
            body: JSON.stringify({ contact: { email_or_username: p } })
        })
        const response = await res.json()
        if(res.error) return res.error
        else return response
    }
}