import DashboardLayout from "@/layout/dashboard/DashboardLayout.vue";
import Login from '@/pages/Login.vue'
import KeyChain from '@/pages/Keychain.vue'
import CreateAccount from '@/pages/CreateAccount.vue'
import ForgotPassword from '@/pages/ForgotPassword.vue'
import NewContact from '@/pages/NewContact.vue'
import Users from "@/pages/Users.vue";

const routes = [
  {
    path: "/",
    component: DashboardLayout,
    redirect: "/keychain",
    children: [
      {
        path: "keychain",
        name: "Keychain",
        props : {
          description: 'Use filters to narrow down to a particular group, status, users…'
        },
        component: KeyChain
      },
      {
        path: "users",
        name: "Users",
        props: {
          description: 'Use filters to find user, user group, or user activity...'
        },
        component: Users
      }
    ]
  },
  {
    path: "/new-contact",
    component: NewContact
  },
  { path: "*", component: DashboardLayout },
  { path: "/login", component: Login },
  { path: "/create-account", component: CreateAccount },
  { path: "/forgot-password", component: ForgotPassword }
];

/**
 * Asynchronously load view (Webpack Lazy loading compatible)
 * The specified component must be inside the Views folder
 * @param  {string} name  the filename (basename) of the view to load.
function view(name) {
   var res= require('../components/Dashboard/Views/' + name + '.vue');
   return res;
};**/

export default routes;
