<template>
    <div class="row">
      <div class="col-12">
       <user-details-drawer v-if="store.state.user.detailsUser !== undefined"></user-details-drawer>
        <card class="card-plain">
          <div class="table-full-width table-responsive">
            <user-table type="hover" :data="table.data"
                         :columns="table.columns" :maxRow="table.data.length">

            </user-table>
          </div>
        </card>
      </div>

    </div>
</template>
<script>
import UserTable from "@/components/UserTable";
import moment from "moment";
import repository from "@/repository/users";
import { userDataRepository } from "@/repository/users";
import UserDetailsDrawer from "@/layout/dashboard/UserDetailsDrawer";
import store from "@/flulux/store";
export default {
  components: {
    UserTable,
    UserDetailsDrawer
  },
  data() {
    return {
      table: {
        title: "",
        columns: ["name", "username", "last activity"],
        dataCopy: [],
        data: []
      },
      store: store
    };
  },
  mounted() {
    repository.retrieveContacts().then(res => {
      this.parseDataTable(userDataRepository.contacts);
      this.setMaxPagination(userDataRepository.contacts.length)
    });
  },
  methods: {
    setMaxPagination(p) {
      if (p <= 20) {
        this.store.actions.SET_END_PAGINATION_USERS(
          p
        );
      } else this.store.actions.SET_END_PAGINATION_USERS(
          20
        );
    },
    parseDataTable(p) {
      let data = [];
      const userIds = p.map(i => i.user.id)
      repository.retrieveContactPictures(userIds).then(res => {
        res.forEach(user => {
           p.forEach(i => {
             if(user.user_id === i.user.id) {
               i.profile_picture = user.base64
             }
           })
        })
        p.map(async i => {
        await data.push({
          id: i.user.id,
          profile_picture: i.profile_picture,
          name: i.user.first_name + " " + i.user.last_name,
          username: i.user.username,
          "last activity": this.getLastAcivity(i.last_activity)
        });
      });
          this.table.data = [...data];
      })
    },
    getLastAcivity(p) {
      if (!p) return "No recent activity";
      return this.getDiffTime(p.updated_at) + " ago";
    },

    getDiffTime(p) {
      const now = moment(p);
      const end = moment();
      const duration = moment.duration(end.diff(now));
      const days = duration.asDays();
      const years = duration.asYears();
      const hours = duration.asHours();
      const minutes = duration.asMinutes();
      const seconds = duration.asSeconds();

      if (years > 1) return 1 + " years ";
      if (Math.round(days) >= 1) return Math.round(days) + " days";
      if (hours >= 1) return hours + " hours ";
      if (minutes >= 1) return minutes + " minutes ";
      if (seconds >= 1) return seconds + " seconds ";
    }
  }
};
</script>
<style>
.card-body {
  overflow: -webkit-paged-x;
}
</style>
