<template>
<div>
    <router-link  to="/login"><img class="close-account-page" src='@/assets/havr-icon/close-drawer-icon.svg'></router-link>
    <div class="form-stepper-create-account">
      <div class="jumbo">
  <h2 ><b>Create your Havr account </b></h2>
  <p >Please enter or click a button to validate a line</p>
      </div>
  <br><br>

        <div :class="{'hide-form' : currentStep < stepper.one}" class="section-form">
     <label class="step-form" for="exampleInputEmail1"><span :class="{baseColor : currentStep === stepper.one}">1</span>. Hello there, what's your firstname ?</label>
<div class="form-inline">
  <div class="form-group">
   
 
    <input @click="currentStep = 1" @keyup.enter="trigger('step2', 'normal', user.first_name)" v-model="user.first_name" type="text" class="form-control form-control-lg custom-size havr-input "  aria-describedby="emailHelp" placeholder="eg. Jhon" required>
    <div class="invalid-feedback">
          Please enter your first name.
        </div>
  </div>
  <button id="formButton" v-if="currentStep === 1 && user.first_name.length > 0"  @click="trigger('step2', 'normal', user.first_name)"   class="btn btn-primary havr-color margin-left" :class="{activeButton : currentStep === stepper.one}">NEXT</button>
</div>

        </div>

 <div :class="{'hide-form' : currentStep < stepper.two}" class="section-form">
<label class="step-form" for="exampleInputEmail1"><span :class="{baseColor : currentStep === stepper.two}">2.</span> And your last name ?</label>
<div class="form-inline">
  <div class="form-group">
   
 
    <input @click="currentStep = 2" @keyup.enter="trigger('step3', 'normal', user.last_name)" v-model="user.last_name" :disabled="user.first_name.length === 0" type="text" class="form-control form-control-lg custom-size havr-input " aria-describedby="emailHelp" placeholder="eg. Doe" ref="step2" required>
     <div class="invalid-feedback">
          Please enter your last name.
        </div>
  </div>
  <button id="formButton" v-if="currentStep === stepper.two && user.last_name.length > 0"  @click="trigger('step3', 'normal', user.last_name)"  class="btn btn-primary havr-color margin-left" :class="{activeButton : currentStep === stepper.two}">NEXT</button>
</div>
 </div>
  <div :class="{'hide-form' : currentStep < stepper.three}" class="section-form">
<label class="step-form" for="exampleInputEmail1"><span :class="{baseColor : currentStep === stepper.three}" >3.</span> Whats your email ?</label>
<div class="form-inline">
  <div class="form-group">
   
 
    <input @click="currentStep = 3" @keyup.enter="trigger('step4', 'email', user.email)"  v-model="user.email" :disabled="user.last_name.length === 0 && currentStep < stepper.three" type="email" class="form-control form-control-lg custom-size havr-input " aria-describedby="emailHelp" placeholder="eg. jhon@doe.com" ref="step3" required>
     <div class="invalid-feedback">
          Please enter your email.
        </div>
  </div>
  <button id="formButton" v-if="currentStep === stepper.three && checkEmailProcess === false"  @click="trigger('step4', 'email', user.email)"  class="btn btn-primary havr-color margin-left" :class="{activeButton : currentStep === stepper.three}">NEXT</button>
  <div v-if="checkEmailProcess" class="sk-circle">
  <div class="sk-circle1 sk-child"></div>
  <div class="sk-circle2 sk-child"></div>
  <div class="sk-circle3 sk-child"></div>
  <div class="sk-circle4 sk-child"></div>
  <div class="sk-circle5 sk-child"></div>
  <div class="sk-circle6 sk-child"></div>
  <div class="sk-circle7 sk-child"></div>
  <div class="sk-circle8 sk-child"></div>
  <div class="sk-circle9 sk-child"></div>
  <div class="sk-circle10 sk-child"></div>
  <div class="sk-circle11 sk-child"></div>
  <div class="sk-circle12 sk-child"></div>
</div>
</div>
  </div>
   <div :class="{'hide-form' : currentStep < stepper.four}" class="section-form">
<label class="step-form" for="exampleInputEmail1"><span :class="{baseColor : currentStep === stepper.four}" >4.</span> Time to choose password</label>
<div class="form-inline">
  <div class="form-group">
   
 
    <input @click="currentStep = 4" @keyup.enter="trigger('step5', 'password', user.password)"  v-model="user.password" :disabled="user.email.length === 0" type="password" class="form-control form-control-lg custom-size havr-input "  aria-describedby="emailHelp" placeholder="8 characters min" ref="step4" required>
     <div class="invalid-feedback">
          Please enter a password.
        </div>
  </div>
  <button  id="formButton" v-if=" currentStep === stepper.four && user.password"  @click="trigger('step5', 'password', user.password)"  class="btn btn-primary havr-color margin-left" :class="{activeButton : currentStep === stepper.four}">NEXT</button>
</div>
   </div>
    <div :class="{'hide-form' : currentStep < stepper.five}" class="section-form">

<label class="step-form" for="exampleInputEmail1"><span :class="{baseColor : currentStep === stepper.five}">5. </span> Please confirm password</label>
<div class="form-inline">
  <div class="form-group">
   
 
    <input @click="currentStep = 5" @keyup.enter="trigger('step6', 'samePassword', checkPasswordData)" v-model="checkPasswordData" :disabled="user.password.length === 0" type="password" class="form-control form-control-lg custom-size havr-input "  aria-describedby="emailHelp" ref="step5" placeholder="same as before" required>
     <div class="invalid-feedback">
          Please confirm your password.
        </div>
  </div>
  <button id="formButton" v-if="checkPasswordData.length > 0 && currentStep === stepper.five "  @click="trigger('step6', 'samePassword', checkPasswordData)" class="btn btn-primary havr-color margin-left" :class="{activeButton : currentStep === stepper.five}">NEXT</button>
</div>

    </div>
     <div :class="{'hide-form' : currentStep < stepper.six}" class="section-form">
<label  class="step-form" for="exampleInputEmail1"><span :class="{baseColor : currentStep === stepper.six}">6.</span> Finally, choose a username</label>

  <div class="form-group">
   
 
    <input @keyup.enter="checkUsernameAndGoNextStep()" v-model="user.username"  type="text" class="form-control form-control-lg custom-size havr-input "  aria-describedby="emailHelp" ref="step6" placeholder="eg. jhon02" required>
     <div class="invalid-feedback">
          Please enter a username.
        </div>
  </div>
  <br>
  <button :disabled="canCreateAccount" v-if="currentStep === stepper.six && !checkUsernameProcess"  @click="checkUsernameAndGoNextStep()"  class="btn btn-primary havr-color margin-left" :class="{activeButton : currentStep === stepper.six}">CREATE ACCOUNT</button>
<div v-if="checkUsernameProcess" class="sk-circle">
  <div class="sk-circle1 sk-child"></div>
  <div class="sk-circle2 sk-child"></div>
  <div class="sk-circle3 sk-child"></div>
  <div class="sk-circle4 sk-child"></div>
  <div class="sk-circle5 sk-child"></div>
  <div class="sk-circle6 sk-child"></div>
  <div class="sk-circle7 sk-child"></div>
  <div class="sk-circle8 sk-child"></div>
  <div class="sk-circle9 sk-child"></div>
  <div class="sk-circle10 sk-child"></div>
  <div class="sk-circle11 sk-child"></div>
