<script>
import repository from "@/repository/users";
import notifiy from "vue-notification";
import Swal from "sweetalert2";
export default {
  data() {
    return {
      checkPasswordData: "",
      currentStep: 1,
      checkEmailProcess: false,
      emailIsAlreadyTaken: false,
      usernameIsAlreadyTaken: false,
      checkUsernameProcess: false,
      user: {
        email: "",
        username: "",
        first_name: "",
        last_name: "",
        password: ""
      },
      stepper: {
        one: 1,
        two: 2,
        three: 3,
        four: 4,
        five: 5,
        six: 6
      },
      passwordRegex: /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d.*)(?=.*\W.*)[a-zA-Z0-9\S]{8,50}$/m,
      usernameRegex: /[^A-Za-z0-9]/gm,
      emailRegex: /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
    };
  },
  computed: {
    canCreateAccount() {
      for (const i in this.user) {
        if (this.user[i].trim() === "") return true;
      }
    }
  },
  methods: {
    checkPassword(p) {
      if (this.user.password.length < 16) {
        const regex = RegExp(this.passwordRegex);
        if (!regex.test(this.user.password)) return this.notifyBadPassword();
      }
        this.currentStep++;
          this.$refs[p].focus();
    },
    checkSamePassword(p) {
      if (this.user.password !== this.checkPasswordData) {
        this.$notify({
          message: "Error : Password dont match",
          type: "danger",
          timeout: 2000,
          horizontalAlign: "right",
          verticalAlign: "top"
        });
        return false;
      }
      this.currentStep++;
      this.$refs[p].focus();
    },
    async checkUsernameAndGoNextStep() {
      const regex = RegExp(this.usernameRegex);
      if (regex.test(this.user.username)) return this.notifyBadFormatUsername();
      this.checkUsernameProcess = true
      const res = await repository.checkUsernameOrEmail(this.user.username);
      this.checkUsernameProcess = false
      if (res.already_exists) {
this.usernameIsAlreadyTaken = true
        return this.notifyEmailOrUsernameError("Username");
      }
      
      else this.createAccount();
    },
    async checkEmailAndGoNextStep(p) {
      const regex = RegExp(this.emailRegex);
      if (!regex.test(this.user.email)) return this.notifyBadFormatEmail("Email");
      this.checkEmailProcess = true
      const res = await repository.checkUsernameOrEmail(this.user.email);
      this.checkEmailProcess = false
      if (res.already_exists) return this.emailIsAlreadyTaken = true;
      else {
        this.emailIsAlreadyTaken = false
        this.currentStep++;
          this.$refs[p].focus();
      }
    },
    trigger(p, type, s) {
      if(s.length === 0) return
      if (type === "email") {
        return this.checkEmailAndGoNextStep(p).then(res => {

          if (this.emailIsAlreadyTaken) return this.notifyEmailOrUsernameError('Email')
        });
      }
      if (type === "password") {
        return this.checkPassword(p)
      }
      if (type === "username") {
        return this.checkUsernameAndGoNextStep().then(res => {
          if (!res) return this.notifyEmailOrUsernameError("Username");
          this.currentStep++;
          this.$refs[p].focus();
        });
      }
      if (type === "samePassword") {
        return this.checkSamePassword(p);
      }
      else {
this.currentStep++;
      this.$refs[p].focus();
      }
      
    },
    notifyEmailOrUsernameError(p) {
      this.$notify({
        message: "Error : " + p + " already taken",
        type: "danger",
        timeout: 2000,
        horizontalAlign: "right",
        verticalAlign: "top"
      });
    },
    notifyBadFormatEmail(p) {
      this.$notify({
        message: "Error : email format is incorrect",
        type: "danger",
        timeout: 2000,
        horizontalAlign: "right",
        verticalAlign: "top"
      });
    },
    notifyBadFormatUsername() {
      this.$notify({
        message: "Error : username format is incorrect",
        type: "danger",
        timeout: 2000,
        horizontalAlign: "right",
        verticalAlign: "top"
      });
    },
    notifyBadPassword() {
      this.$notify({
        message: "Error : password format is incorrect",
        type: "danger",
        timeout: 2000,
        horizontalAlign: "right",
        verticalAlign: "top"
      });
    },
    async createAccount() {
      if (this.checkPasswordData !== this.user.password) return;
      const user = { user: this.user };
      const resp = await repository.createUserAccount(user);
      this.successNotification();
    },
    successNotification() {
      return Swal({
        allowOutsideClick: false,
        title: "Success",
        text: "Your account has been created with success !",
        type: "success",
        showCancelButton: false,
        confirmButtonText: "Go to login page"
      }).then(result => {
        if (result.value) {
          this.$router.push("login");
        }
      });
    }
  }
};
</script>


