<template>
<div id="wrapper" :class="{'toggled': store.state.user.drawer === false}">

    <div id="sidebar-wrapper">
        <ul class="sidebar-nav">
            <li class="sidebar-brand">
              <transition name="fade">
               <img v-if="store.state.user.drawer === true"  @click="store.actions.OPEN_USER_DETAIL_DRAWER(false)" class="close-icon close-img" src="@/assets/havr-icon/close-drawer-icon.svg">
              </transition>
            </li>
<center>
            <li>
               <transition name="fade">
                <img v-if="store.state.user.detailsUser.profile_picture" class="avatar-profile" :src="'data:image/png;base64,'+ store.state.user.detailsUser.profile_picture">
                  <img v-else class="avatar-profile" src="@/assets/img/usermock.jpeg">
               </transition>
            </li>


           
            <li>
              <span class="user-name">{{store.state.user.detailsUser.user.first_name}} {{store.state.user.detailsUser.user.last_name}} </span>
              <br>
                <span class="user-tag" href="#">{{store.state.user.detailsUser.user.username}}</span>
                
            </li>
            <br>
                    </center>
           
                  <ul class="item-icon">
                    <li class="sub-item share-key">
                      <img  src="@/assets/havr-icon/share.svg">
                   <span class="item-sub-text ">share key</span>
                   
</li>
 <li class="sub-item add-to-group">
   <img  src="@/assets/havr-icon/group.svg">
                  <span class="item-sub-text "> add to a group</span>
</li>
 <li class="sub-item flag">
   <img  src="@/assets/havr-icon/flag.svg">
                   <span class="item-sub-text ">flag</span>
                    </li>
                  </ul>
                  <hr>
                  <li class="item">
                   <p class="sub-nav-user" href="#">Has access to {{store.state.user.detailsUser.shared_keys.length}} keys</p>
                  <span v-if="indexShowAll === 3"  @click="expandShowAll()" class="showAll">show all</span>
                  <span v-else @click="expandShowAll()" class="showAll">close</span>
                  </li>
                   
                  
                    <li v-for="item in store.state.user.detailsUser.shared_keys.slice(0, indexShowAll)">
              
                     <span style="display: flex;margin-bottom: 5px;">
                       <div class="valid" v-if=" item.door_lock.existing_status === 'active'"><i class="ti-check light"></i></div>
                       <div  v-if="item.door_lock.existing_status === 'inactive'"><i class="ti-close inactive"></i></div>
                        {{item.door_lock.description}}
                        </span>
                    </li>

                    <hr>
                

                  <li class="item">
                 <p class="sub-nav-user" href="#">{{store.state.user.detailsUser.user.first_name}}'s activity</p>
                  </li>
 <div v-if="store.state.user.detailsUser.logs.length > 0">
            <li  v-for="i in orderLog" class="item">
                  <p class="text-item">  <span v-if="i.door_lock_status === 'locked'">closed</span>
                  <span v-else>opened </span>  {{getLockDoorFromId(i.door_lock_id)}}</p>
              
                   <p class="sub-log timing-user"> <span>{{getDiffTime(i.created_at)}} ago</span></p>
                  </li>

                    
</div>
             
<li v-if="store.state.user.detailsUser.logs.length === 0">
                    <p class="text-item">No recent activity</p>
                  </li>
        </ul>
    </div>
</div>

</template>
<script>
import store from "@/flulux/store";
import moment from "moment";
import lodash from "lodash";
import { userDataRepository } from "@/repository/keychain";
export default {
  data() {
    return {
      store: store,
      userDataRepository: userDataRepository,
      indexShowAll: 3
    };
  },
  computed: {
    orderLog: function() {
      return this.store.state.user.detailsUser.logs.sort(
        (a, b) => new Date(b.created_at) - new Date(a.created_at)
      );
    }
  },
  methods: {
    expandShowAll() {
      if (
        this.indexShowAll ===
        this.store.state.user.detailsUser.shared_keys.length
      ) {
        return (this.indexShowAll = 3);
      }
      return (this.indexShowAll = this.store.state.user.detailsUser.shared_keys.length);
    },

    getLockDoorFromId(p) {
       return lodash.filter(this.store.state.user.detailsUser.shared_keys, {door_lock_id: p})[0].door_lock.description
    },
    /**
     * get time notification
     */
    getDiffTime(p) {
      const now = moment(p);
      const end = moment();
      const duration = moment.duration(end.diff(now));
      const days = duration.asDays();
      const years = duration.asYears();
      const hours = duration.asHours();
      const minutes = duration.asMinutes();
      const seconds = duration.asSeconds();

      if (years > 1) return 1 + " years ";
      if (Math.round(days) >= 1) return Math.round(days) + " days";
      if (hours >= 1) return hours + " hours ";
      if (minutes >= 1) return minutes + " minutes ";
      if (seconds >= 1) return seconds + " seconds ";
    }
  }
};
</script>
