<template>
    <div class="row">
      <div class="col-12">
        <right-details-drawer v-if="store.state.keychain.detailsKey !== undefined" />
        <card class="card-plain">
          <div class="table-full-width table-responsive">
            <paper-table type="hover" :data="table.data"
                         :columns="table.columns" :maxRow="table.data.length">

            </paper-table>
          </div>
        </card>
      </div>

    </div>
</template>
<script>
import { PaperTable } from "@/components";
import moment from "moment";
import repository from "@/repository/keychain";
import { keychainRepository } from "@/repository/keychain";
import RightDetailsDrawer from "@/layout/dashboard/RightDetailsDrawer";
import store from "@/flulux/store";
export default {
  components: {
    PaperTable,
    RightDetailsDrawer
  },
  data() {
    return {
      table: {
        title: "",
        columns: ["key name", "access", "status", "sharing"],
        data: []
      },
      store: store
    };
  },
  mounted() {
    repository.getDoorLocks().then(res => {
      repository.getSharedK().then(res => {
        this.parseDataTable();
        this.store.actions.SET_NB_ROW_KEYCHAIN_TABLE(this.table.data.length);
        this.setMaxPagination(this.table.data.length)
      });
    });
  },
  methods: {
    setMaxPagination(p) {
      if (p <= 20) {
        this.store.actions.SET_END_PAGINATION_KEYCHAIN(
          p
        );
      }
      else  this.store.actions.SET_END_PAGINATION_KEYCHAIN(
          20
        );
    },
    parseDataTable() {
      let res = []
      keychainRepository.door_locks.map((i, index) => {
        res.push({
          id: i.id,
          "key name": i.description,
          access: "owner",
          status: i.existing_status,
          sharing: i.nb_users - 1 + " users"
        });
      });
      keychainRepository.shared_key.map((i, index) => {
        res.push({
          id: i.door_lock_id,
          shared: true,
          "key name": i.door_lock.description,
          access: this.checkAccess(i.admin),
          status: this.checkStatus(i),
          sharing: i.door_lock.nb_users - 1 + " users"

        });
      });
      this.table.data = res
    },
    /**
     * return access status from param
     */
    checkAccess(p) {
      if (p) {
          return "admin";
        } 
      else return "Normal";
    },
    /**
     * return key status from param
     */
    checkStatus(p) {
      if (!p.start_date && !p.end_date) return "unlimited";
      if (moment(p.start_date) > moment()) return "active in" + this.getDiffTime(p.start_date, 'start');
      if (p.end_date !== null && moment(p.end_date) < moment()) return 'inactive';
      if (p.end_date !== null && moment(p.end_date) > moment()) return 'end in ' + this.getDiffTime(p.end_date, 'end');
      else return p.existing_status
    },
    /**
     * get difference time beetween two dates
     */
    getDiffTime(p) {
      const now = moment(p);
      const end = moment();
      const duration = moment.duration(end.diff(now));
      const days = duration.asDays();
      duration.subtract(moment.duration(days, "days"));
      const hours = duration.hours();
      duration.subtract(moment.duration(hours, "hours"));
      const minutes = duration.minutes();
      duration.subtract(moment.duration(minutes, "minutes"));
       const seconds = duration.minutes();
      duration.subtract(moment.duration(seconds, "seconds"));
       if(Math.round(days) >= 1) return Math.round(days) + ' days'
       if(hours >= 1) return hours + ' hours '
       if(minutes >= 1) return minutes + ' minutes ' 
       if(seconds >= 1) return seconds + ' seconds ' 
    }
  }
};
</script>
<style>
.card-body {
  overflow: -webkit-paged-x;
}
</style>
