<template>
<div>
    <router-link  to="/keychain"><img class="close-account-page" src='@/assets/havr-icon/close-drawer-icon.svg'></router-link>
    <div class="form-stepper-new-contact">
      <div class="jumbo">
  <h2 ><b>Invite members to Havr</b></h2>
      </div>
  <br>

        <div class="section-form">
<div>
  <div class="form-group">
   
   <div v-for="(input, i) in contact">
    <input  v-model="contact[i]" type="text"  class="form-control form-control-lg custom-size havr-input" id="inputForm" aria-describedby="emailHelp" :placeholder="'Teammate #' + (i + 1) + ' email'" required>
     <br>
   </div>
   
<p><b class="pointer" @click="addInput(1)">Add another</b></p>
    
  </div>
  <button :disabled="!contact[0]" @click="sendInvitation()"    class="btn btn-primary havr-color" >Send</button>
</div>

        </div>

<img class="side-brand" src="@/assets/img/havr-side-brand.png">
    </div>
    
</div>
</template>



<script>
import repository from "@/repository/users";
import notifiy from "vue-notification";
import Swal from "sweetalert2";
import contactRepository from "@/repository/contact";

export default {
  data() {
    return {
      contact: [null],
      emailRegex: /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
    };
  },
  methods: {
    addInput(p) {
      if (p === 1) {
        this.contact.push(null);
      }
    },
    sendInvitation() {
      this.contact.forEach(async item => {
          if(!item) return
        if (!this.emailRegex.test(item)) {
          const res = await this.notifyBadEmail(item);
          return;
        }
        const res = await contactRepository.addContact(item);
        if (res.error) {
          return this.notifyAddContact("danger", res.error);
        }
        this.notifyAddContact("success", item + " is invited with success");
      });
    },
    notifyAddContact(p1, p2) {
      this.$notify({
        message: p2,
        type: p1,
        timeout: 2000,
        horizontalAlign: "right",
        verticalAlign: "top"
      });
    },
    notifyBadEmail(item) {
      this.$notify({
        message: "Error : " + item + " is an incorrect mail format",
        type: "danger",
        timeout: 2000,
        horizontalAlign: "right",
        verticalAlign: "top"
      });
    }
  }
};
</script>


