<template>
<div>
    <router-link  to="/login"><img class="close-account-page" src='@/assets/havr-icon/close-drawer-icon.svg'></router-link>
    <div class="form-stepper-create-account">
      <div class="jumbo">
  <h2 ><b>Lost password </b></h2>
  <p v-if="!existingUser">Dont't worry, we'got you back !</p>
   <p v-if="existingUser">We sent an email to {{email_or_username}} to reset your password</p>
   <p v-if="existingUser">Just check your emails and follow the instructions</p>
      </div>
  <br>

        <div v-if="!existingUser"  class="section-form">
<div>
  <div class="form-group">
   
 
    <input v-model="email_or_username" type="text"  class="form-control form-control-lg custom-size havr-input" id="inputForm" aria-describedby="emailHelp" placeholder="Enter your email or username" required>
    
  </div>
  <button id="formButton" @click="checkEmailAndGoNextStep()"   class="btn btn-primary havr-color" >Sign in</button>
</div>

        </div>

<img class="side-brand" src="@/assets/img/havr-side-brand.png">
    </div>
    
</div>
</template>



<script>
import repository from "@/repository/users";
import notifiy from "vue-notification";
import Swal from "sweetalert2";
export default {
  data() {
    return {
      email_or_username: '',
      existingUser: false
    };
  },
  methods: {
    async checkEmailAndGoNextStep() {
      const res = await repository.checkUsernameOrEmail(this.email_or_username);
      this.existingUser = res.already_exists
      if (!this.existingUser) return this.notifyEmailOrUsernameError();
    },
    notifyEmailOrUsernameError(p) {
      this.$notify({
        message: "Error : email or username is not recognized",
        type: "danger",
        timeout: 2000,
        horizontalAlign: "right",
        verticalAlign: "bottom"
      });
    },
    successNotification() {
    }
  }
};
</script>


