import {Dispatcher} from './dispatcher'
import store from './store'
export const SET_PAGINATION_KEYCHAIN = Dispatcher.$on('SET_PAGINATION_KEYCHAIN', p => store.state.keychain.selectedItemByPage = p)
export const SET_NB_ROW_KEYCHAIN = Dispatcher.$on('SET_NB_ROW_KEYCHAIN', p => store.state.keychain.rowKeyChain = p)
export const SET_PAGINATION_USERS = Dispatcher.$on('SET_PAGINATION_USERS', p => store.state.user.selectedItemByPage = p)
export const SET_NB_ROW_USERS = Dispatcher.$on('SET_NB_ROW_USERS', p => store.state.user.rowKeyChain = p)
export const SET_DETAILS_KEY = Dispatcher.$on('SET_DETAILS_KEY', p => store.state.keychain.detailsKey = p)
export const OPEN_DRAWER = Dispatcher.$on('OPEN_DRAWER', p => store.state.keychain.drawer = p)
export const OPEN_USER_DRAWER = Dispatcher.$on('OPEN_USER_DRAWER', p => store.state.user.drawer = p)
export const SET_LOG = Dispatcher.$on('SET_LOG', p => store.state.keychain.keylog = p)
export const SET_MANAGEMENT = Dispatcher.$on('SET_MANAGEMENT', p => store.state.keychain.accessManagement = p)
export const SET_START_KEYCHAIN = Dispatcher.$on('SET_START_KEYCHAIN', p => store.state.keychain.startPaginationItem = p)
export const SET_END_KEYCHAIN = Dispatcher.$on('SET_END_KEYCHAIN', p => store.state.keychain.endPaginationItem = p)
export const SET_START_USERS = Dispatcher.$on('SET_START_USERS', p => store.state.user.startPaginationItem = p)
export const SET_END_USERS = Dispatcher.$on('SET_END_USERS', p => store.state.user.endPaginationItem = p)
