<script>
import { dataRepository } from "@/repository/login";
import store from "@/flulux/store";
import moment from "moment";
import lodash from "lodash";
import repository from "@/repository/keychain";
import { keychainRepository } from "@/repository/keychain";
export default {
  data() {
    return {
      store: store,
      keychainRepository: keychainRepository
    };
  },
  computed: {
       orderLog: function() {
       return this.store.state.keychain.keylog.sort((a, b) => new Date(b.created_at) - new Date(a.created_at))
    }
  },
  methods: {
    /**
     * get time notification
     */
    getDiffTime(p) {
      const now = moment(p);
      const end = moment();
      const duration = moment.duration(end.diff(now));
      const days = duration.asDays();
      const years = duration.asYears();
      const hours = duration.asHours();
      const minutes = duration.asMinutes();
      const seconds = duration.asSeconds();

      if (years > 1) return 1 + " years ";
      if (Math.round(days) >= 1) return Math.round(days) + " days";
      if (hours >= 1) return hours + " hours ";
      if (minutes >= 1) return minutes + " minutes ";
      if (seconds >= 1) return seconds + " seconds ";
    }
  }
};
</script>
