<template>
    <div class="limiter">
	
		<div class="container-login100">
			<div class="wrap-login100">
				    
					<div class="login100-more">
						<img class="cover-img" src="@/assets/img/havrassembly@3x.png">
					</div>
			<div class="form-container">
					<div class="login100-form validate-form">
							<span class="login100-form-title p-b-43">
								<img  src="@/assets/img/logo-final-havr-01.jpg">
							</span>
								<input placeholder="Email or username" v-model="state.email_or_username"  class="form-control form-control-lg custom-size havr-input" type="text" name="text">
							<br>
							<br>
								<input  placeholder="Password" v-model="state.password" class="form-control form-control-lg custom-size havr-input" type="password"  name="pass">
							<div class="flex-sb-m w-full p-t-3 p-b-32 forgot-password">
								<div class="contact100-form-checkbox">
								
								</div>
		
								<div>
								<router-link  to="/forgot-password">
										<p class="forgot-password-label">Forgot Password ?</p>

								</router-link>
								</div>

							
							</div>
					
		<br>
							<div class="container-login100-form-btn">
								<button :disabled="state.password === '' || state.email_or_username === ''" @click="login" class="login100-form-btn havr-color">
									Sign in
								</button>
							</div>
							<router-link  to="/create-account"><p class="new-account">First time here ? <b>Create an account</b></p></router-link>
						</div>
			</div>
				

				
			</div>
		</div>
	</div>
	
</template>

<script>
import loginRepository from "@/repository/login";
import { dataRepository } from "@/repository/login";
export default {
  data() {
    return {
      state: {
        grant_type: "password",
        email_or_username: "",
        password: "",
        client_id: "web_plateform_client_id",
        user_agent: navigator.userAgent
      }
    };
  },
  mounted() {
		 //this.checkIfUserIsConnected() 
  },
  methods: {
    /**
     * log user into application using data repository
     */
    login() {
      loginRepository
        .login(this.state)
        .then(res => this.checkIfUserIsConnected());
    },
    /**  * check acccess token props to log user
     */
    checkIfUserIsConnected() {
			if (!dataRepository.account.access_token) return this.showErrMsg();
      else this.$router.push("/keychain");
		},
    /**     * bind error msg if credentials are wrong
     */
    showErrMsg() {
      this.$notify({
        group: "foo", 
        title: "Username or password is incorrect",
        type: 'danger'
      });
    }
  }
};
</script>



<style lang="scss" scoped>
@import "../assets/sass/login.scss";
</style>
