<template>
  <footer class="footer fixed">
    <notifications class="alert-info"   group="foo" />
    <div class="container-fluid d-flex flex-wrap justify-content-between">
      <nav>
        <ul>
          <li>
             <button :disabled="store.state.keychain.rowKeyChain < 20" :class="{'activeButton': store.state.keychain.selectedItemByPage === 20 }" @click="setPaginationTable(20)" class="item-table-selector pointer selector-last-page center-selector left-selector ">20</button>
             <button :disabled="store.state.keychain.rowKeyChain < 50" :class="{'activeButton': store.state.keychain.selectedItemByPage === 50 }" @click="setPaginationTable(50)" class="item-table-selector pointer center-selector">50</button>
             <button :disabled="store.state.keychain.rowKeyChain < 100" :class="{'activeButton': store.state.keychain.selectedItemByPage === 100 }" @click="setPaginationTable(100)" class="item-table-selector pointer selector-next-page center-selector right-selector ">100</button> keys per page
          </li>
          <li class="fixed-bottom-right">
            {{store.state.keychain.startPaginationItem}} - {{store.state.keychain.endPaginationItem}} of {{filterRow}}
             <button :disabled="store.state.keychain.startPaginationItem === 1" @click="move('backward')" class="item-table-selector arrow pointer selector-last-page"><i class="ti-angle-left grey"></i></button>
             <button @click="move('forward')" class="item-table-selector arrow pointer selector-next-page" ><i class="ti-angle-right grey"></i></button>
          </li>
        </ul>
      </nav>
      <div class="copyright d-flex flex-wrap">
       
      </div>
    </div>
  </footer>
</template>
<script>
import store from "@/flulux/store";
export default {
  data() {
    return {
      store: store
    };
  },
  mounted() {
    console.log(store, "store content footer")
    this.setPaginationTable(20)
  },
  computed: {
    filterRow() {
       if(store.state.keychain.rowKeyChain > 20) return store.state.keychain.rowKeyChain
       else return store.state.keychain.endPaginationItem
    }
  },
  methods: {
    /**
     * map pagination to data store
     */
    setPaginationTable(p) {
      if (store.state.keychain.rowKeyChain < p) {
        this.store.actions.SET_END_PAGINATION_KEYCHAIN(
          store.state.keychain.rowKeyChain
        );
      } else {
        this.store.actions.SET_PAGINATION_KEYCHAIN_TABLE(p);
        this.store.actions.SET_END_PAGINATION_KEYCHAIN(p);
      }
    },
    move(state) {
      if (state === "forward") {
        if (
          store.state.keychain.selectedItemByPage +
            store.state.keychain.startPaginationItem >
            store.state.keychain.rowKeyChain ||
          store.state.keychain.selectedItemByPage +
            store.state.keychain.endPaginationItem +
            1 >
            store.state.keychain.rowKeyChain
        )
          return this.setPaginationTable(store.state.keychain.rowKeyChain + 1);
        this.store.actions.SET_START_PAGINATION_KEYCHAIN(
          store.state.keychain.selectedItemByPage +
            store.state.keychain.startPaginationItem
        );
        this.store.actions.SET_END_PAGINATION_KEYCHAIN(
          store.state.keychain.selectedItemByPage +
            store.state.keychain.endPaginationItem +
            1
        );
      } else {
        if (
          store.state.keychain.selectedItemByPage -
            store.state.keychain.startPaginationItem >
            store.state.keychain.startPaginationItem ||
          store.state.keychain.selectedItemByPage -
            store.state.keychain.endPaginationItem >
            store.state.keychain.endPaginationItem
        )
          return this.setPaginationTable(store.state.keychain.rowKeyChain + 1);
        this.store.actions.SET_START_PAGINATION_KEYCHAIN(
          store.state.keychain.startPaginationItem -
            store.state.keychain.selectedItemByPage
        );
        this.store.actions.SET_END_PAGINATION_KEYCHAIN(
          store.state.keychain.endPaginationItem -
            1 -
            store.state.keychain.selectedItemByPage
        );
      }
    }
  }
};
</script>
<style>
.fixed {
  z-index: 999999999 !important;
  position: fixed !important;
  bottom: 0;
  width: 83%;
  height: 60px;
  background: #fdfdfd;
}

.item-table-selector {
  border: none;
  width: 40px;
  height: 30px;
}

.selector-last-page {
    border-bottom-left-radius: 7px;
    border-top-left-radius: 7px;
}

i.ti-angle-left.grey {
    vertical-align: middle;
}

i.ti-angle-right.grey {
    vertical-align: middle;
}

.selector-next-page {
    border-bottom-right-radius: 7px;
    border-top-right-radius: 7px;
}

.activeButton {
  background: #a6a6a6;
  border-radius: 12%;
  color: white;
}
.arrow {
  border: 1px solid #e6e6e6;

}

.center-selector {
      border-left: 0px;
    border-right: 0px;
    border-top: 1px solid #E6E6E6;
    border-bottom: 1px solid #E6E6E6;
}
.right-selector {
    border-right: 1px solid #E6E6E6;
}

.left-selector {
    border-bottom-right-radius: 0px;
    border-top-right-radius: 0px;
}


.grey {
  color: #a6a6a6;
}
.pointer {
  cursor: pointer;
}
.fixed-bottom-right {
  position: fixed;
  right: 0;
}
</style>
