<template>
  <component :is="tag"
             class="dropdown"
             :class="{show:isOpen}"
             @click="toggleDropDown"
             v-click-outside="closeDropDown">
    <a class="nav-drop"
       :class="titleClasses"
       data-toggle="dropdown">
      <slot name="title">
       <img v-if="!type" src="@/assets/havr-icon/filterIcon.svg">
        <span> {{title}} <i v-if="type" class="fa fa-arrow-down" aria-hidden="true"></i></span>
      </slot>
    </a>
    <ul class="dropdown-menu" :class="{show:isOpen}">
      <slot></slot>
    </ul>
  </component>
</template>
<script>
export default {
  props: {
    tag: {
      type: String,
      default: "li"
    },
    type: String,
    title: String,
    icon: String,
    titleClasses: [String, Object, Array]
  },
  data() {
    return {
      isOpen: false
    };
  },
  methods: {
    toggleDropDown() {
      this.isOpen = !this.isOpen;
      this.$emit("change", this.isOpen);
    },
    closeDropDown() {
      this.isOpen = false;
      this.$emit('change', false);
    }
  }
};
</script>


<style>
.nav-drop {
      position: initial;
}
</style>
