<template>
  <nav class="navbar navbar-expand-lg navbar-light">
    <div class="container-fluid fixed-header">
      <ul class="header-content">
        <li>
           <a class="navbar-brand" href="#">{{routeName}}</a>
        </li>
      <li class="subheader-top">
        <span>{{getDescription}}</span>
         <li>
        <br> 
        <button class="transparent" @click="filterShow()">
        <drop-down   
                     title="Filter"
                     title-classes="nav-link specific-dropdown"
                     icon="">
          
          </drop-down>
        </button>
      </li>
      <li>
         <div  v-if="routeName === 'Keychain'" class="selector-item"> <p class="item-text"><img class="selector-img" src="@/assets/havr-icon/list.svg"> list </p><p class="item-text"><img class="selector-img" src="@/assets/havr-icon/map-marker.svg"> map</p></div>
      </li>
      <transition name="fade">
       <li class="sub-filter" v-if="showFilters">
          
       <div class="filter-section">
        
         <drop-down 
                     title="Key group"
                     title-classes="nav-link specific-dropdown left-margin"
                     icon="">
          
          </drop-down>
          <drop-down 
                     title="User group"
                     title-classes="nav-link specific-dropdown left-margin "
                     icon="">
          
          </drop-down>
          <drop-down 
                     title="Users"
                     title-classes="nav-link specific-dropdown left-margin"
                     icon="">
          
          </drop-down>
          <drop-down 
                     title="Lock status"
                     title-classes="nav-link specific-dropdown left-margin"
                     icon="">
          
          </drop-down>
       </div>
         
      </li>

     </transition>
      </ul>
      <button class="navbar-toggler navbar-burger filter-button"
              type="button"
              @click="toggleSidebar"
              :aria-expanded="$sidebar.showSidebar"
              aria-label="Toggle navigation">
        <span class="navbar-toggler-bar"></span>
        <span class="navbar-toggler-bar"></span>
        <span class="navbar-toggler-bar"></span>
      </button>
      <div class="collapse navbar-collapse">
        <ul class="navbar-nav ml-auto">
        </ul>
      </div>
    </div></nav>
</template>
<script>
export default {
  computed: {
    routeName() {
      const { name } = this.$route;
      return this.capitalizeFirstLetter(name);
    },
    getDescription() {
      let description = ''
      this.$route.matched.forEach(elm => {
        if (elm.name === this.$route.name) {
          description = elm.props.default.description
        }
      });
      return description;
    }
  },
  data() {
    return {
      showFilters: false,
      activeNotifications: false,
      description: ""
    };
  },

  methods: {
    capitalizeFirstLetter(string) {
      return string.charAt(0).toUpperCase() + string.slice(1);
    },
    filterShow() {
      this.showFilters = !this.showFilters;
    },
    toggleNotificationDropDown() {
      this.activeNotifications = !this.activeNotifications;
    },
    closeDropDown() {
      this.activeNotifications = false;
    },
    toggleSidebar() {
      this.$sidebar.displaySidebar(!this.$sidebar.showSidebar);
    },
    hideSidebar() {
      this.$sidebar.displaySidebar(false);
    }
  }
};
</script>
<style>
ul {
  list-style-type: none;
}
.header-content {
  margin-top: 0;
  height: 200px;
  position: relative;
  margin-bottom: -4rem;
  width: 100%;
  background: white
}
li {
  list-style-type: none;
}
.subheader-top {
  position: relative;
  bottom: 15px;
}

.specific-dropdown {
  width: 95px;
  border-radius: 6px;
  line-height: 15px;
  margin-bottom: 1em;
  height: 32px;
  border: solid 1px #e6e6e6;
}

.left-margin {
  margin-right: 2em;
  width: 200px;
}

.transparent {
  background: transparent;
  border: none;
  position: initial;
}

.fixed-header {
  position: fixed;
  top: -1em;
  background: white;
  width: 70%;
  z-index: 10;
}

.filter-button {
      position: relative;
    right: 8px;
}

.selector-item {
  float: right;
  position: relative;
  bottom: 3em;
  display: flex;
  left: -20em;
}

.item-text {
  font-weight: bold;
  margin-left: 40px;
}

.selector-img {
  margin-right: 16px;
}

.nav-link {
      position: relative;
    top: -7px!important;
}

.filter-section {
  display: flex;
  position: relative;
  top: 1em;
  width: 100%;
  height: 140px;
}
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.5s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}

.container-fluid.fixed-header {
  width: 100%;
}

.sub-filter {
       border-top: 1px solid #e6e6e6;
    background: white;
    position: a;
    margin-left: -3em;
}
</style>
